.productCard {
    width: 14vmax;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: rgb(48, 48, 48);
    margin: 2vmax;
    transition: all 0.5s;
    padding: 0.5vmax;
    border-radius: 5%;
    height: 25vmax;
    width: 15vmax;
}
.productCard>.img {
    display: flex;
    justify-content: center;
    align-items: center;
}
.productCard>.img>img {
    height:14vmax;
    width: min-content;
}

.productCard>div {
    margin: 0.5vmax;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.productCard>div>.span {
    margin: 0.5vmax;
    font: 300 1vmax "Roboto";
}

.productCard>p {
    font-family: "Roboto";
    font-size: 1.2vmax;
    margin: 1vmax 0.5vmax;
    margin-bottom: 0;
}

.productCard>span {
    margin: 0.5vmax;
    color: tomato;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.productCard:hover {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transform: translateY(-1vmax) scale(1.1);
 
}

@media screen and (max-width:600px) {

    .productCard>p {
        font-size: 2.5vmax;
    }

    .productCard>div {
        margin: 0vmax;
        display: block;

    }

    .productCard>span {
        font-size: 2.2vmax;
    }

    .productCard>div>span {
        
        font: 300 20px "Roboto";
        
    }
    .productCard>div>.span {
        
        font: 300 2vmax "Roboto";
    }
}