.productsContainer{
    display: grid;
    grid-template-areas: 'heading heading heading  heading heading  '
                        'filterBox filterBox products products  products'
                        'pagination pagination pagination pagination pagination  ';

}



#container{
    scroll-margin-top: 3vmax;

}

.homeHeading{
    grid-area: heading;
    text-align: center;
    font-size: 1.4vmax;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 20vmax;
    border-bottom: 1px solid rgba(21,21,21,0.5);
    padding: 1vmax;
    margin: 3vmax auto;
    color: rgba(0,0,0,0.7);
}



.products{
    grid-area: products;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 80vw;
    margin: 2vmax auto;
    position: relative;
    min-height: 60vh;

}



.paginationBox{
    grid-area: pagination;
    display: flex;
    justify-content: center;
    margin: 6vmax;
}

.pagination{
    display: flex;
    justify-content: center;
    padding: 0;
}

.page-item{
    background-color: rgb(255, 255, 255);
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;

}
.page-item:first-child {
    border-radius:  5px 0 0 5px;

}
 .page-item:last-child{
    border-radius: 0 5px 5px 0;
}

.page-link{
    text-decoration: none;
    font: 300 0.7vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
}

.page-item:hover{
    background-color: rgb(230, 230, 230);
}

.page-item:hover .page-link{
    color: rgb(80, 80, 80);
}

.pageItemActive{
    background-color: tomato;
}

.pageLinkActive{
    color: #fff;

}

.filterBox{
    grid-area: filterBox;
    width: 10vmax;
    margin: auto;
    /* position: absolute;
    top: 10vmax;
    left: 3vmax; */


}

.filterBox > ul{
    margin-bottom: 2vmax;
}


.noProduct{
    font: 500 2vmax cursive;

}
@media screen and (max-width:600px) {
    .products{
        width: 100vw;
    }
    .filterBox{
        width: 20vmax;
        position: static;
        margin: auto;
    }
    .page-link{
    font: 300 1.7vmax "Roboto";

    }
    .productsContainer{
        grid-template-areas: 'heading  '
                            '  products'
                            '  filterBox'
                            ' pagination  ';
    
    }
    

    
}


